.profile__avatar {
    width: 120px;
    height: 120px;
    margin-right: 30px;
    border-radius: 50%;
    object-fit: cover;
    background-position: center;
  }
  
  @media screen and (max-width: 580px) {
    .profile__avatar {
      width: 80px;
      height: 80px;
    }
  }
  
  @media screen and (max-width: 480px) {
    .profile__avatar {
      width: 120px;
      height: 120px;
      margin-right: 0;
      margin-bottom: 26px;
      align-self: center;
    }
  }